(function ($) {
  'use strict';

  function setupAutoSubmitFilter(element, form) {
    form.on('change', element, function () {
      form.submit();
    });
  }

  Drupal.behaviors.documentLibrary = {
    attach: function (context, settings) {
      if (context === document) {
        var form = $('.ListingView--documentLibrary .views-exposed-form', context);

        setupAutoSubmitFilter('.FormItem--document-type .form-select', form);
      }
    }
  };
}(jQuery));
